<template>
  <div>
    <!-- 面包屑 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>编辑商品</el-breadcrumb-item>
    </el-breadcrumb>

    <!-- 列表卡片 -->
    <el-card class="box-card">
      <!-- 提示 -->
      <el-alert
        title="编辑商品信息（请重新上传图片！）"
        type="info"
        show-icon
        :closable="false"
        center
      >
      </el-alert>

      <!-- 进度条 -->
      <el-steps :active="activeIndex - 0" finish-status="success" align-center>
        <el-step title="基本信息"></el-step>
        <el-step title="商品参数"></el-step>
        <el-step title="商品属性"></el-step>
        <el-step title="商品图片"></el-step>
        <el-step title="商品内容"></el-step>
        <el-step title="完成"></el-step>
      </el-steps>
      <el-form
        :model="editForm"
        :rules="editFormRules"
        ref="editFormRef"
        label-width="100px"
        label-position="top"
        class="demo-ruleForm"
      >
        <el-tabs
          v-model="activeIndex"
          :tab-position="tabPosition"
          style="height: 560px;"
          :before-leave="handleLeave"
          @tab-click="tabClick"
        >
          <!-- 基本信息 -->
          <el-tab-pane label="基本信息" name="0">
            <el-form-item label="商品名称" prop="goods_name">
              <el-input v-model="editForm.goods_name"></el-input>
            </el-form-item>
            <el-form-item label="商品价格" prop="goods_price">
              <el-input v-model="editForm.goods_price" type="number"></el-input>
            </el-form-item>
            <el-form-item label="商品重量" prop="goods_weight">
              <el-input v-model="editForm.goods_weight" type="number"></el-input>
            </el-form-item>
            <el-form-item label="商品数量" prop="goods_number">
              <el-input v-model="editForm.goods_number" type="number"></el-input>
            </el-form-item>
            <!-- 级联选择器 -->
            <el-form-item label="商品分类">
              <el-cascader
                v-model="selectedKeys"
                :options="cateList"
                :props="cascaderProps"
                @change="handleChange"
              ></el-cascader>
            </el-form-item>
          </el-tab-pane>

          <!-- 商品参数 -->
          <el-tab-pane label="商品参数" name="1">
            <el-form-item
              :label="item.attr_name"
              v-for="(item, i) in manyList"
              :key="i"
            >
              <!-- 复选框 -->
              <el-checkbox-group v-model="item.attr_vals">
                <el-checkbox
                  :label="item2"
                  v-for="(item2, i2) in item.attr_vals"
                  :key="i2"
                  border
                ></el-checkbox>
              </el-checkbox-group>
            </el-form-item>
          </el-tab-pane>

          <!-- 商品属性 -->
          <el-tab-pane label="商品属性" name="2">
            <el-form-item
              :label="item.attr_name"
              v-for="item in onlyList"
              :key="item.attr_id"
            >
              <el-input type="text" v-model="item.attr_vals"></el-input>
            </el-form-item>
          </el-tab-pane>

          <!-- 商品图片 -->
          <el-tab-pane label="商品图片" name="3">
            <p>先前上传的图片：</p>
            <div v-if="imgList !== []">
              <img :src="item" alt="之前上传图片" v-for="(item,i) in imgList" :key="i" class="existImg">
            </div>
            <p v-else>空</p>
            <!-- action图片提交地址 -->
            <el-upload
              class="upload-demo"
              action="http://127.0.0.1:8888/api/private/v1/upload"
              :on-preview="handlePreview"
              :on-remove="handleRemove"
              :headers="uploadHeaders"
              :on-success="uploadSuccess"
              list-type="picture"
            >
              <el-button size="small" type="primary">点击上传</el-button>
            </el-upload>
          </el-tab-pane>

          <!-- 商品内容 -->
          <el-tab-pane label="商品内容" name="4">
            <!-- 富文本编辑器 -->
            <quill-editor v-model="editForm.goods_introduce"></quill-editor>
            <!-- 提交按钮 -->
            <el-button type="primary" style="margin-top: 15px" @click="addGood">确定</el-button>
          </el-tab-pane>
        </el-tabs>
      </el-form>
    </el-card>

    <!-- 图片预览框 -->
    <el-dialog
      title="图片预览"
      :visible.sync="previewVisible"
      width="50%"
    >
      <img :src="previewImgUrl" alt="图片预览" class="imgSize">
    </el-dialog>
  </div>
</template>

<script>
// 导入vuex辅助函数
import { mapState, mapMutations, mapGetters } from 'vuex'

export default {
  name: 'GoodsAdd',
  created() {
    // 重置vuex中selectedArray的值和表格manyList onlyList的值
    this.handleChange(this.selectedKeys)
    // 获取三级分类数据
    this.getCateList()
    // 获取商品信息数据
    this.getGoodlist()
  },
  data() {
    return {
      tabPosition: 'left',
      activeIndex: 0,
      editForm: {},
      manyAttrs: [],
      onlyAttrs: [],
      editFormRules: {
        goods_name: [
          { required: true, message: '请输入商品名称', trigger: 'blur' }
        ],
        goods_price: [
          { required: true, message: '请输入商品价格', trigger: 'blur' }
        ],
        goods_weight: [
          { required: true, message: '请输入商品重量', trigger: 'blur' }
        ],
        goods_number: [
          { required: true, message: '请输入商品数量', trigger: 'blur' }
        ]
      },
      selectedKeys: [],
      goodList: [],
      cascaderProps: {
        expandTrigger: 'hover',
        value: 'cat_id',
        label: 'cat_name',
        children: 'children'
      },
      // 上传图片请求头加上token
      uploadHeaders: {
        Authorization: window.sessionStorage.getItem('token')
      },
      // 图片预览URL
      previewImgUrl: '',
      previewVisible: false,
      cateList: [],
      // 要获取信息的商品ID
      updateGoodId: window.sessionStorage.getItem('updateGoodId'),
      imgList: []
    }
  },
  methods: {
    ...mapMutations(['getCateParamsVuex', 'handleChangeVuex']),
    // 根据ID获取商品数据
    async getGoodlist() {
      const { data: res } = await this.$http.get(`goods/${this.updateGoodId}`)
      if (res.meta.status !== 200) return this.$message.error('获取商品信息失败')
      this.editForm = res.data
      // 初始化数据
      // 三级分类
      this.selectedKeys = this.editForm.goods_cat.split(',').map(item => parseInt(item))
      // 动态参数  静态属性
      this.editForm.attrs.forEach(item => {
        if (item.attr_sel === 'many') {
          this.manyList.push(item)
        } else {
          this.onlyList.push(item)
        }
      })
      // 将manyList中的attr_vals转化为数组
      this.manyList.map(item => {
        item.attr_vals = item.attr_vals.split(' ')
      })
      // 分别存进对应的数组
      this.manyAttrs = this.setAttrs(this.manyList)
      this.onlyAttrs = this.setAttrs(this.onlyList)
      // 图片列表
      this.editForm.pics.forEach(item => {
        this.imgList.push(item.pics_big_url)
      })
      this.resetEditForm()
    },
    // 重置editForm
    resetEditForm() {
      // eslint-disable-next-line prefer-const
      let temObj = {}
      temObj.goods_name = this.editForm.goods_name
      temObj.goods_price = this.editForm.goods_price
      temObj.goods_number = this.editForm.goods_number
      temObj.goods_weight = this.editForm.goods_weight
      temObj.goods_introduce = this.editForm.goods_introduce
      temObj.goods_cat = this.editForm.goods_cat
      // 把pics清空
      temObj.pics = []
      // 清空attrs
      temObj.attrs = []
      this.editForm = temObj
    },
    // 获取三级分类列表数据
    async getCateList() {
      const { data: res } = await this.$http.get('categories', {
        params: { type: 3 }
      })
      if (res.meta.status !== 200) {
        return this.$message.error('获取分类列表数据失败')
      }
      this.cateList = res.data
    },
    // 级联选择器选择变化事件
    handleChange() {
      // 传入第二个参数 让onlyList的attr_vals不被转为数组，保留原字符串格式
      this.handleChangeVuex({
        selectedKeys: this.selectedKeys,
        stringFormat: true
      })
      if (this.selectedKeys.length === 3) {
        this.editForm.goods_cat = this.selectedKeys.join(',')
      }
    },
    // 获取参数
    getCateParams(paramType) {
      this.getCateParamsVuex(paramType)
    },
    // tabs before-leave钩子函数事件
    handleLeave(activeName, oldActiveName) {
      // 若未选择商品分类则不能切换标签页
      if (oldActiveName === '0' && this.selectedKeys.length !== 3) {
        this.$message.error('请选择商品分类')
        return false
      } else if (oldActiveName === '1') {
        this.manyAttrs = this.setAttrs(this.manyList)
      } else if (oldActiveName === '2') {
        this.onlyAttrs = this.setAttrs(this.onlyList)
      }
    },
    setAttrs(list) {
      // 将改变后的数据存储进对应的数组
      // eslint-disable-next-line prefer-const
      let temArray = []
      list.forEach(item => {
        // eslint-disable-next-line prefer-const
        let obj = {}
        obj.attr_id = item.attr_id
        if (list !== this.onlyList) {
          obj.attr_value = item.attr_vals.join(' ')
        } else {
          obj.attr_value = item.attr_vals
        }
        temArray.push(obj)
      })
      return temArray
    },
    // 标签页点击事件
    tabClick() {},
    // 点击预览
    handlePreview(file) {
      this.previewImgUrl = file.response.data.url
      // 弹出预览框
      this.previewVisible = true
    },
    // 上传取消
    handleRemove(file) {
      const index = this.editForm.pics.findIndex(
        item => item.pic === file.response.data.tmp_path
      )
      this.editForm.pics.splice(index, 1)
    },
    // 上传成功
    uploadSuccess(response) {
      // 最后需要一起提交的图片地址
      // eslint-disable-next-line prefer-const
      let obj = { pic: '' }
      obj.pic = response.data.tmp_path
      this.editForm.pics.push(obj)
    },
    // 添加提交
    addGood() {
      // 预验证
      this.$refs.editFormRef.validate(async valid => {
        if (!valid) return
        // 预验证通过 发起请求
        this.editForm.attrs = [...this.manyAttrs, ...this.onlyAttrs]
        const { data: res } = await this.$http.put(`goods/${this.updateGoodId}`, this.editForm)
        if (res.meta.status !== 200) return this.$message.error('修改商品信息失败')
        this.$message.success('修改商品信息成功')
        // 重定向回goodsList页面
        this.$router.push('/goods')
      })
    }
  },
  computed: {
    ...mapState(['manyList', 'onlyList', 'selectedArray']),
    ...mapGetters(['cateId'])
  }
}
</script>

<style lang="less" scoped>
.el-step__title {
  font-size: 15px;
}
.el-form {
  margin: 25px 10px 0px;
}
.imgSize {
  width: 100%;
}
.existImg {
  width: 100px;
  height: 100px;
}
.upload-demo {
  margin-top: 10px;
}
</style>
