<template>
  <div>
    <!-- 面包屑 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>商品列表</el-breadcrumb-item>
    </el-breadcrumb>

    <!-- 列表卡片 -->
    <el-card class="box-card">
      <!-- 搜索框和添加按钮 -->
      <el-row :gutter="10" type="flex" justify="center">
        <el-col :span="10">
          <el-input
            v-model="queryInfo.query"
            clearable
            placeholder="请输入内容"
            @clear="getGoodsList"
          >
            <el-button
              slot="append"
              icon="el-icon-search"
              @click="getGoodsList"
            ></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="gotoAddGoodsPage"
            >添加商品</el-button
          >
        </el-col>
      </el-row>

      <!-- 用户列表表格 -->
      <el-table border stripe :data="goodsList">
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column label="商品" prop="goods_name"></el-table-column>
        <el-table-column
          width="110px"
          label="商品价格(元)"
          prop="goods_price"
        ></el-table-column>
        <el-table-column
          width="80px"
          label="商品重量"
          prop="goods_weight"
        ></el-table-column>
        <el-table-column width="160px" label="创建时间">
          <template v-slot="data">
            {{ data.row.add_time | dateFormat }}
          </template>
        </el-table-column>
        <el-table-column width="150px" label="操作">
          <!-- 修改编辑 -->
          // eslint-disable-next-line vue/no-unused-vars
          <template v-slot="data">
            <el-tooltip
              effect="dark"
              content="编辑"
              placement="top"
              :enterable="false"
            >
              <el-button
                type="primary"
                icon="el-icon-edit"
                size="mini"
                @click="gotoUpdateGoodsPage(data.row.goods_id)"
              ></el-button>
            </el-tooltip>
            <!-- 删除用户 -->
            <el-tooltip
              effect="dark"
              content="删除"
              placement="top"
              :enterable="false"
            >
              <el-button
                type="danger"
                icon="el-icon-delete"
                size="mini"
                @click="deleteGood(data.row.goods_id)"
              ></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>

      <!-- 分页页码 -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[8, 10, 15, 20]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
        background
      >
      </el-pagination>
    </el-card>
  </div>
</template>

<script>
export default {
  name: 'GoodsList',
  created() {
    this.getGoodsList()
  },
  data() {
    return {
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 10
      },
      goodsList: [],
      total: 0
    }
  },
  methods: {
    async getGoodsList() {
      const { data: res } = await this.$http.get('goods', {
        params: this.queryInfo
      })
      if (res.meta.status !== 200) { return this.$messzge.error('获取商品列表数据失败') }
      this.goodsList = res.data.goods
      this.total = res.data.total
    },
    // pagesize change
    handleSizeChange(newPagesize) {
      this.queryInfo.pagesize = newPagesize
      this.getGoodsList()
    },
    // pagenum change
    handleCurrentChange(newPagenum) {
      this.queryInfo.pagenum = newPagenum
      this.getGoodsList()
    },
    // 删除商品
    async deleteGood(id) {
      const confirmResult = await this.$confirm(
        '此操作将永久删除该商品数据, 是否继续?',
        '提示',
        {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }
      ).catch(err => err)

      if (confirmResult !== 'confirm') return this.$message.info('已取消删除')

      const { data: res } = await this.$http.delete(`goods/${id}`)

      if (res.meta.status !== 200) { return this.$message.error('删除商品数据失败') }

      // 提示删除成功
      this.$message.success('删除商品数据成功')
      // 重新获取商品列表数据
      this.getGoodsList()
    },
    // 跳转到商品添加页面
    gotoAddGoodsPage() {
      this.$router.push('/goods/add')
    },
    // 跳转到编辑页面
    gotoUpdateGoodsPage(id) {
      window.sessionStorage.setItem('updateGoodId', id)
      this.$router.push('/goods/update')
    }
  }
}
</script>

<style lang="less" scoped></style>
